import styles from "./Signin.module.css";

function SignInInputField({
  label,
  value,
  onChange,
  type = "text",
  errorMessage,
}) {
  return (
    <div className={styles.signInTextBox}>
      <div className={styles.frame31}>
        <input
          className={`${styles.placeholder} ${
            errorMessage ? styles.errorInput : ""
          }`}
          value={value}
          onChange={onChange}
          type={type}
          placeholder={errorMessage ? ` ${errorMessage}` : label} // placeholder prop  label .
          // aria-describedby={error ? "error-msg" : undefined}
        />
      </div>
      <div className={styles.nameLabel}>{label}</div>
    </div>
  );
}
export default SignInInputField;
