import React, { useState } from "react";
import SignInInputField from "./SignInInputField.jsx";
import SignInButton from "./SignInButton.jsx";
import { useAuth } from "../AuthContext.jsx";
import styles from "./Signin.module.css";

function SignIn() {
  const { login } = useAuth();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [errors, setErrors] = useState({});

  const validate = () => {
    let tempErrors = {};
    tempErrors.email = email ? "" : "ID .";
    tempErrors.password =
      password.length >= 6 ? "" : " 6  .";
    setErrors(tempErrors);
    return Object.values(tempErrors).every((x) => x === "");
  };

  const handleIdChange = (event) => setEmail(event.target.value);
  const handlePasswordChange = (event) => setPassword(event.target.value);
  const onSubmit = async (event) => {
    event.preventDefault();
    if (validate()) {
      try {
        await login(email, password);
        //   ,     .
      } catch (error) {
        //   ,   .
        setErrors({
          ...errors,
          form: " .    .",
        });
      }
    } else {
      console.error("Validation failed.");
    }
  };

  return (
    <div className={styles.signInArea}>
      <form className={styles.frame33} onSubmit={onSubmit}>
        <SignInInputField
          label="email"
          value={email}
          onChange={handleIdChange}
          errorMessage={errors.email}
        />
        <SignInInputField
          label="Password"
          type="password"
          value={password}
          onChange={handlePasswordChange}
          errorMessage={errors.password}
        />
        {errors.form && <div className={styles.error}>{errors.form}</div>}{" "}
        {/*       */}
        <SignInButton onSubmit={onSubmit} label="Sign-In" />
      </form>
    </div>
  );
}
export default SignIn;
