import React, { useState } from "react";
import SignInInputField from "./SignInInputField.jsx";
import SignInButton from "./SignInButton.jsx";
import { useAuth } from "../AuthContext.jsx";
import styles from "./Signin.module.css";
import axios from "axios";
import { useNavigate } from "react-router-dom";

function SignUp({ switchToSignIn, close }) {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [errors, setErrors] = useState({});
  const navigate = useNavigate();
  const { URL } = useAuth();

  const validateField = (key, value) => {
    let tempErrors = { ...errors };
    if (key === "name") {
      tempErrors.name = value ? "" : "name .";
    } else if (key === "email") {
      tempErrors.email = value ? "" : "email .";
    } else if (key === "password") {
      tempErrors.password =
        value.length >= 6 ? "" : " 6  .";
    } else if (key === "confirmPassword") {
      tempErrors.confirmPassword =
        password === value ? "" : " .";
    }

    setErrors(tempErrors);
  };

  const handleNameChange = (event) => {
    setName(event.target.value);
    validateField("name", event.target.value);
  };
  const handleEmailChange = (event) => {
    setEmail(event.target.value);
    validateField("email", event.target.value);
  };
  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
    validateField("password", event.target.value);
  };
  const handleConfirmPasswordChange = (event) => {
    setConfirmPassword(event.target.value);
    validateField("confirmPassword", event.target.value);
  };

  const validate = () => {
    let tempErrors = {};
    tempErrors.name = name ? "" : "name .";
    tempErrors.email = email ? "" : "email .";
    tempErrors.password =
      password.length >= 6 ? "" : " 6  .";
    tempErrors.confirmPassword =
      password === confirmPassword ? "" : " .";

    setErrors(tempErrors);

    return Object.values(tempErrors).every((x) => x === "");
  };

  const onSubmit = async (event) => {
    event.preventDefault();
    if (validate()) {
      const data = {
        nickname: name,
        username: email,
        password: password,
        // confirmPassword     .
      };
      // console.log(data);

      const SIGNUP_URL = `${URL}/api/account/register/`;
      try {
        const response = await axios.post(SIGNUP_URL, data);
        console.log(response.data);
        //      .
        // :   ,    
        close();
        navigate("/");
        alert(
          `Welcome to Pureplate, ${response.data.nickname}!\nPlease, sign in!`
        );
      } catch (error) {
        console.error(
          "Error during sign up:",
          error.response ? error.response.data : error
        );
        //     .
        // :     
      }
    } else {
      console.error("Validation failed.");
    }
  };

  return (
    <form className={styles.frame33} onSubmit={onSubmit}>
      <SignInInputField
        label="Name"
        value={name}
        onChange={handleNameChange}
        errorMessage={errors.name}
      />
      <SignInInputField
        label="email"
        type="email"
        value={email}
        onChange={handleEmailChange}
        errorMessage={errors.email}
      />
      <SignInInputField
        label="Password"
        type="password"
        value={password}
        onChange={handlePasswordChange}
        errorMessage={errors.password}
      />
      <SignInInputField
        label="Confirm Password"
        type="password"
        value={confirmPassword}
        onChange={handleConfirmPasswordChange}
        errorMessage={errors.confirmPassword}
      />
      <SignInButton onSubmit={onSubmit} label="submit" />
    </form>
  );
}

export default SignUp;
